@media (min-width: 1440px) {
    .first-screen {
        width: 100%;
        margin: 80px auto auto auto;
        display: flex;
        align-items: center;
        justify-content: space-between;

        &__title {
            display: flex;
            flex-direction: column;

            &--text {
                font-size: 12px;
                line-height: 20px;
                letter-spacing: 2px;
                text-transform: uppercase;
                color: #A8A8A8;
            }

            &--name {
                font-size: 38px;
                line-height: 48px;
                color: #000000;
            }
        }
    }
}