@media (min-width: 1440px) {
    .promo {
        position: relative;
        width: 100%;
        height: 800px;
        background: linear-gradient(287.15deg, #450E4B 0%, #3C0C41 24.48%, rgba(207, 0, 99, 0) 100%), #CF0063;
        border-radius: 0px;
        z-index: 1;

        &-box {
            margin-top: 177px;
            margin-left: 144px;
            display: flex;

            &__title {

                &--name {
                    font-size: 80px;
                    line-height: 80px;
                    color: #FFFFFF;
                    margin-bottom: 0;
                    margin-top: 0;
                }
    
                &--text {
                    font-size: 30px;
                    line-height: 44px;
                    color: #FFFFFF;
                }
    
                &--btn {
                    width: 170px;
                    height: 66px;
                    background: #FC0A7E;
                    border-radius: 3px;
                    border: none;
    
                    &>span {
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 26px;
                        text-align: center;
                        color: #FFFFFF;
                    }
                }
            }

            &__img {
                position: absolute;
                top: 8em;
                right: 44px;
            }
        }
    }
}