@media (min-width: 1440px) {
    .header {
        padding-left: 144px;
        padding-right: 144px;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: auto;

        &-links {
            width: 380px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            &__item {
                &>a {
                    font-size: 15px;
                    color: #FFFFFF;
                }
            }
        }
    }
}