@media (min-width: 1440px) {
    .device {
        width: 100%;
        height: 957px;
        background: #1B1B1B;
        border-radius: 0px;
        display: flex;
        flex-direction: column;
        align-items: center;

        &-title {
            margin-top: 100px;

            &__name {
                font-size: 12px;
                line-height: 20px;
                text-align: center;
                letter-spacing: 2px;
                text-transform: uppercase;
                color: #A8A8A8;
            }

            &__text {
                width: 607px;
                font-size: 38px;
                line-height: 48px;
                text-align: center;
                color: #FFFFFF;
            }
        }

        &-img {
            margin-top: 10px;
        }
    }
}